import React from 'react'
import style from '../style-sheets/Cards.module.scss'
import Card from './Card.jsx'

export default function Cards({pokemons}) {

    return (

        <div className={style.cards_container}>

            {pokemons.length > 0 ? pokemons.map(p => <Card key={p.name + p.id} img={p.img} name={p.name} types={p.types} id={p.id} /> ) : <img className={style.gif} src={'https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/8ee4b875-457c-4f95-8dc5-0817243a235a/d8ogz4y-a89404b5-cbfe-43ae-af86-1f46f45687bc.gif?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..WZnwNsqwI_04nmi8uhfnmmGtsJaFT3KBhiwVI6cx9ZI'} alt='pokeball gif' /> }
        
        </div>

    )
}
